









































































import Vue from "vue";
import Count from "../components/sales/dialogs/Count.vue";

export default Vue.extend({
  name: "Sales",

  data: () => ({
    item: undefined as Record<any, any> | undefined,
  }),

  computed: {
    hasItems() {
      if (this.billItems && this.billItems.length > 0) {
        return true;
      } else {
        return false;
      }
    },
    billItems() {
      return this.$store.getters["sales/billItems"];
    },
    cats() {
      return this.$store.getters["sales/cats"];
    },
    items() {
      return this.$store.getters["sales/items"];
    },
  },
  methods: {
    saveBill() {
      this.$store.dispatch("sales/saveBill");
    },
    openCount(item: Record<any, any>) {
      this.item = item;
      this.$store.dispatch("sales/dialog/count");
    },
    removeFromBill(id: number) {
      this.$store.dispatch("sales/removeFromBill", id);
    },
  },
  components: {
    Count,
  },
  mounted() {
    this.$store.dispatch("sales/initial");
  },
  beforeDestroy() {
    this.$store.dispatch("sales/reset");
  },
});
