import { Module } from 'vuex';
import { Item, Cat } from './types';
import dialog from './dialog';
import { sendReq } from '../../api';

const defaultState = () => ({
  receipts: [],
  items: [] as Item[],
  cats: [] as Cat[],
});

export default {
  namespaced: true,
  state: defaultState(),
  modules: {
    dialog,
  },
  mutations: {
    confirm(state, { id }) {
      for (const receipt of state.receipts) {
        if (receipt.id == id) {
          state.receipts.splice(state.receipts.indexOf(receipt), 1);
          for (const item of receipt.items) {
            for (const si of state.items) {
              if (si.id == item.id) si.count += parseInt(item.count);
            }
          }
        }
      }
    },
    editPrice(state, { id, cid, price }) {
      for (const item of state.items) {
        if (item.id == id && item.cid == cid) {
          item.price = price;
        }
      }
    },
    initial(state, { items, cats, receipts }) {
      state.cats = cats;
      state.items = items;
      state.receipts = receipts;
    },
    reset(state) {
      Object.assign(state, defaultState());
    },
  },
  actions: {
    async confirm({ commit }, id) {
      await sendReq('/stock/confirm', 'put', { id });
      commit('confirm', { id });
    },
    async editPrice({ commit }, { iid, cid, price }) {
      const { id } = await sendReq('/stock/price', 'put', { iid, cid, price });
      commit('editPrice', { id, cid, price });
    },
    async initial({ commit, state }) {
      const { items, cats, receipts } = await sendReq('/stock');
      cats.unshift({ id: 0, name: 'كل اﻷصناف' });
      commit('initial', { items, cats, receipts });
    },
    async reset({ commit }) {
      commit('reset');
    },
  },
  getters: {
    receipts: (state) => state.receipts,
    items: (state) => state.items,
    cats: (state) => state.cats,
  },
} as Module<any, any>;
