import { Module } from 'vuex';
import { Item, Cat } from './types';
import dialog from './dialog';
import { sendReq } from '../../api';

const defaultState = () => ({
  items: [] as Item[],
  cats: [] as Cat[],
  billItems: [],
});

export default {
  namespaced: true,
  state: defaultState(),

  modules: {
    dialog,
  },

  mutations: {
    saveBill(state, counts) {
      for (const ci of counts) {
        for (const item of state.items) {
          if (item.id == ci.id) item.count = ci.count;
        }
      }
      state.billItems = [];
    },
    removeFromBill(state, id) {
      for (const x in state.billItems) {
        if (state.billItems[x].id == id) state.billItems.splice(x, 1);
      }
    },
    addToBill(state, item) {
      for (const x in state.billItems) {
        if (state.billItems[x].id == item.id) {
          state.billItems.splice(x, 1, item);
          return;
        }
      }
      state.billItems.push(item);
    },
    initial(state, { cats, items }) {
      state.cats = cats;
      state.items = items;
    },
    reset(state) {
      Object.assign(state, defaultState());
    },
  },
  actions: {
    async saveBill({ commit, state }) {
      const { counts } = await sendReq('/sales/bill', 'post', {
        items: state.billItems,
      });
      commit('saveBill', counts);
    },
    removeFromBill({ commit }, id) {
      commit('removeFromBill', id);
    },
    addToBill({ commit }, item) {
      commit('addToBill', item);
    },
    async initial({ commit }) {
      const { cats, items } = await sendReq('/sales');
      cats.unshift({ id: 0, name: 'كل اﻷصناف' });
      commit('initial', { cats, items });
    },
    async reset({ commit }) {
      commit('reset');
    },
  },
  getters: {
    items: (state) => state.items,
    cats: (state) => state.cats,
    billItems: (state) => state.billItems,
  },
} as Module<any, any>;
