



































































import Vue from "vue";
import NewReceipt from "./dialogs/NewReceipt.vue";

export default Vue.extend({
  data: () => ({}),
  computed: {
    receiptItems() {
      return this.$store.getters["purchases/receiptItems"];
    },
    isReady() {
      if (this.receiptItems.length <= 0) return false;
      for (const item of this.receiptItems) {
        if (!item.count || item.count <= 0 || !item.price || item.price <= 0) {
          return false;
        }
      }
      return true;
    },
  },
  methods: {
    remove(id: number, cid: number) {
      this.$store.dispatch("purchases/removeFromReceipt", { id, cid });
    },
  },
  components: {
    NewReceipt,
  },
});
