
























import Vue from "vue";
export default Vue.extend({
  data: () => ({}),
  props: {
    receipt: {
      type: Object,
      required: true,
    },
  },
  computed: {
    dialog(): any {
      return this.$store.getters["stock/dialog/confirm"];
    },
  },
  methods: {
    confirm() {
      this.$store
        .dispatch("stock/confirm", this.receipt.id)
        .then(() => this.closeMe());
    },
    closeMe() {
      this.$store.dispatch("stock/dialog/confirm");
    },
  },
});
