


































import Vue from "vue";
import NewCat from "./dialogs/NewCat.vue";
import ShowItems from "./dialogs/ShowItems.vue";

export default Vue.extend({
  name: "Cats",

  data: () => ({}),
  computed: {
    cats() {
      return this.$store.getters["purchases/cats"];
    },
  },
  methods: {
    showItems(cid: number) {
      this.$store.dispatch("purchases/setSelectedCat", cid);
      this.$store.dispatch("purchases/dialog/showItems");
    },
  },
  components: {
    NewCat,
    ShowItems,
  },
});
