import { Module } from 'vuex';
import { Cat, ReceiptItem, Unit } from './types';
import dialog from './dialog';
import { sendReq } from '../../api';

const defaultState = () => ({
  selectedCat: undefined as Cat | undefined,
  receiptItems: [] as ReceiptItem[],
  cats: [] as Cat[],
  units: undefined as Unit[] | undefined,
});

export default {
  namespaced: true,
  state: defaultState(),
  modules: {
    dialog,
  },
  mutations: {
    setSelectedCat(state, cid) {
      if (!cid) return (state.selectedCat = undefined);
      for (const cat of state.cats) {
        if (cat.id == cid) {
          state.selectedCat = cat;
        }
      }
    },
    removeFromReceipt(state, { id, cid }) {
      state.receiptItems.forEach((item: ReceiptItem, index: number) => {
        if (item.id == id && item.cid == cid) {
          state.receiptItems.splice(index, 1);
        }
      });
    },
    addToReceipt(state, id) {
      for (const item of state.receiptItems) {
        if (item.id == id) return;
      }
      for (const cat of state.cats) {
        const cid = state.selectedCat.id;
        if (cat.id == cid) {
          for (const item of cat.items) {
            if (item.id == id) {
              state.receiptItems.push({
                id,
                name: `${cat.name} ${item.name}`,
                unit: cat.unit,
                cid,
              });
            }
          }
        }
      }
    },
    addItem(state, { item, cid }) {
      for (const cat of state.cats!) {
        if (cat.id == cid) {
          cat.items.push(item);
        }
      }
    },
    addCat(state, cat) {
      cat['items'] = [];
      state.cats!.push(cat);
    },
    initial(state, { cats, units }) {
      state.cats = cats;
      state.units = units;
    },
    reset(state) {
      Object.assign(state, defaultState());
    },
  },
  actions: {
    async saveReceipt({ state }) {
      if (state.receiptItems.length == 0) throw new Error('No Receipt Items');
      await sendReq('/purchases/receipt', 'post', {
        items: state.receiptItems,
      });
      state.receiptItems = [];
    },
    async setSelectedCat({ commit }, cid) {
      commit('setSelectedCat', cid);
    },
    async removeFromReceipt({ commit }, { id, cid }) {
      if (!id || !cid) throw new Error('No Id Or cid');
      commit('removeFromReceipt', { id, cid });
    },
    async addToReceipt({ commit }, { id, cid }) {
      if (!id || !cid) throw new Error('No Id Or cid');
      commit('addToReceipt', { id, cid });
    },
    async addItem({ commit, state }, name) {
      const cat = state.selectedCat;
      try {
        if (!name || !cat) throw new Error('No Name Or cat');
        const { id } = await sendReq('/purchases/item', 'post', {
          name,
          cid: cat.id,
        });
        commit('addItem', {
          item: { id, name },
          cid: cat.id,
        });
      } catch (error) {
        console.log(error);
      }
    },
    async addCat({ commit }, { name, uid }) {
      try {
        if (!name || !uid) throw new Error('No Name Or unit');
        const { id } = await sendReq('/purchases/cat', 'post', {
          name,
          uid,
        });
        commit('addCat', { id, name });
      } catch (error) {
        console.log(error);
      }
    },
    async initial({ commit, state }) {
      if (state.cats.length == 0) {
        const { cats, units } = await sendReq('/purchases');
        commit('initial', { cats, units });
      }
    },
    async reset({ commit }) {
      commit('reset');
    },
  },
  getters: {
    selectedCat: (state: any) => state.selectedCat,
    receiptItems: (state: any) => state.receiptItems,
    cats: (state: any) => state.cats,
    units: (state: any) => state.units,
  },
} as Module<any, any>;
