




























import Vue from "vue";
import _ar from "@/_ar";

export default Vue.extend({
  name: "App",

  data: () => ({ section: "" }),
  computed: {
    currentSection() {
      return this.$store.getters["account/currentSection"];
    },
  },
  watch: {
    currentSection: {
      immediate: true,
      handler: function (val) {
        if (val) {
          this.section = _ar[val];
          const section = `/${val}`;
          if (this.$route.path != section) {
            this.$router.replace(section);
          }
        } else {
          if (this.$route.path != "/") {
            this.$router.replace("/");
          }
        }
      },
    },
  },
  methods: {
    leave() {
      this.section = "";
      this.$store.dispatch("account/leave");
    },
  },
});
