






















































import Vue from "vue";
import _ar from "../_ar";

export default Vue.extend({
  name: "Home",

  data: () => ({
    valid: true,
    pin: "",
    pinRules: [(v: any) => !!v || "رمز الدخول مطلوب"],
  }),

  computed: {
    currentSection() {
      return this.$store.getters["account/currentSection"];
    },
    pins() {
      const arr = [];
      const _pins = this.$store.getters["account/pins"];
      for (const key of Object.keys(_pins)) {
        arr.push({ key: _ar[key], val: _pins[key] });
      }
      return arr;
    },
  },

  methods: {
    enter() {
      if ((this.$refs.form as HTMLFormElement)!.validate()) {
        this.$store.dispatch("account/enter", this.pin);
      }
    },
  },

  mounted() {
    (this.$refs["input-pin"] as HTMLInputElement)!.focus();
  },
});
