
























import Vue from "vue";
export default Vue.extend({
  data: () => ({
    count: "",
  }),
  props: {
    item: {
      required: true,
      type: Object,
    },
  },
  computed: {
    dialog(): any {
      return this.$store.getters["sales/dialog/count"];
    },
  },
  watch: {
    dialog: {
      handler(val) {
        if (val == false)
          (this.$refs["input-count"] as HTMLFormElement).reset();
      },
    },
  },
  methods: {
    addToBill() {
      const count = parseInt(this.count);
      if (!count || count <= 0) return;
      const item = Object.assign({}, this.item);
      item.count = this.count;
      item.price *= parseInt(this.count);
      this.$store.dispatch("sales/addToBill", item).then(() => this.closeMe());
    },
    closeMe() {
      this.$store.dispatch("sales/dialog/count");
    },
  },
});
